import React from 'react';
import { CurrentUserContext } from '../contexts/currentUserContext';
import PopupWithForm from './PopupWithForm';

export default function EditProfilePopup({isOpen, onClose, onUpdateUser, submitButtonText, loadingText, isLoading}) {

  // –ü–æ–¥–ø–∏—Å–∫–∞ –Ω–∞ –∫–æ–Ω—Ç–µ–∫—Å—Ç
  const currentUser = React.useContext(CurrentUserContext);

  const [name, setName] = React.useState(currentUser.name);
  const [description, setDescription] = React.useState(currentUser.name);
  let aaa = currentUser.name;
  let bbb = currentUser.name;

  const [nameValue, setNameValue] = React.useState(aaa);
  const [descriptionValue, setDescriptionValue] = React.useState(bbb);


  React.useEffect(() => {
    setName(currentUser.name);
    setDescription(currentUser.about);
  }, [currentUser]);

  // –ü–æ—Å–ª–µ –∑–∞–≥—Ä—É–∑–∫–∏ —Ç–µ–∫—É—â–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏–∑ API
  // –µ–≥–æ –¥–∞–Ω–Ω—ã–µ –±—É–¥—É—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω—ã –≤ —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞—Ö.
  function handleSubmit(e) {
    // –ó–∞–ø—Ä–µ—â–∞–µ–º –±—Ä–∞—É–∑–µ—Ä—É –ø–µ—Ä–µ—Ö–æ–¥–∏—Ç—å –ø–æ –∞–¥—Ä–µ—Å—É —Ñ–æ—Ä–º—ã
    e.preventDefault();

    // –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
    onUpdateUser({
      name: nameValue,
      about: descriptionValue
    });
  }

  return (
    <PopupWithForm
      name="profle"
      title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å"
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      submitButtonText={submitButtonText}
      loadingText={loadingText}
      isLoading={isLoading}
      children={(
        <>
          <div className="popup__form-item-group">
            <input
            id="username"
            className="popup__form-item popup__form-item_input_name"
            type="text"
            placeholder="–ò–º—è"
            defaultValue={name}
            onChange={(e) => {setNameValue(e.target.value)}}
            name="name"
            minLength="2"
            maxLength="40"
            required />
            <span id="username-error" className="popup__form-error"></span>
          </div>
          <div className="popup__form-item-group">
            <input
            id="description"
            className="popup__form-item popup__form-item_input_description"
            type="text"
            placeholder="–û–ø–∏—Å–∞–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è"
            defaultValue={description}
            onChange={(e) => setDescriptionValue(e.target.value)}
            name="about"
            minLength="2"
            maxLength="200"
            required />
            <span id="description-error" className="popup__form-error"></span>
          </div>
        </>
      )}
  />)
}
